import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import {
    setTreeData,
    setTreeTemBillData,
    setTreeTemPrintData,
    setExpandedKeys,
    setSelectedKeys,
    setDef1,
    setSelectedTemKeys,
    setExpandedTemKeys,
    setTemplatePk,
    setSearchValue,
    setPageCode,
    setAppCode,
    setParentIdcon,
    setTemplateNameVal,
    setTemplateTitleVal,
    setOrgidObj,
    setNodeKey,
    setCopyId,
    setJson,
    setTemplateType
} from "Store/TemplateSetting-unit/action";
import { Modal, Tree, Input } from "antd";
import {
    PageLayout,
    PageLayoutHeader,
    PageLayoutLeft,
    PageLayoutRight
} from "Components/PageLayout";
import { createTree } from "Pub/js/createTree";
import Ajax from "Pub/js/ajax.js";
import Notice from "Components/Notice";
import { ControlTip } from "Components/ControlTip";
import ButtonCreate from "Components/ButtonCreate";
import BusinessUnitTreeRefUnit from "Components/Refers/BusinessUnitTreeRefUnit";
import PreviewModal from "Components/TempletPreview";
import AssignComponent from "./assignComponent";
import CopyMoudle from "./copyMoudle";
import CopyOthersMoudle from "./copyOthersMoudle";
import ReNameTemplet from "./ReNameTemplet";
import { openPage } from "Pub/js/superJump";
import Svg from "Components/Svg";
import { generateTemData, generateTreeData } from "./method";
import { getMulti } from "Pub/js/getMulti";
import { langCheck, DeferFn, GetQuery } from "Pub/js/utils";
import Appicon from "Components/Appicon";
import { handleDownload } from "Pub/js/formDownload";
import "./index.less";
import platform from "Pub/js/platform";
const { NCDiv } = platform.base;
const TreeNode = Tree.TreeNode;
const Search = Input.Search;
const { NCButton, NCUpload } = platform.base;
class TemplateSettingUnit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            treeData: [],
            autoExpandParent: true,
            autoExpandTemParent: true,
            treeTemBillData: [], //单据模板数据
            treeTemPrintData: [],
            treePrintTemData: [],
            visible: false,
            alloVisible: false,
            previewPrintVisible: false,
            previewPrintContent: "",
            batchSettingModalVisibel: false,
            copyOthersModalVisible: false,
            reNameModal: false,
            saveLeftArrTS: []
        };
    }
    //取消
    setCopyVisible = visible => {
        this.setState({
            visible: visible
        });
    };
    //复制到其他应用modal取消
    setCopyOthersVisible = copyOthersModalVisible => {
        this.setState({
            copyOthersModalVisible
        });
    };
    //重命名modal取消
    setReNameModal = reNameModal => {
        this.setState({
            reNameModal
        });
    };
    //按钮事件的触发
    handleClick = code => {
        const {
            def1,
            templatePk,
            appCode,
            orgidObj,
            json,
            pageCode
        } = this.props;
        let infoData = {
            templateId: templatePk
        };
        if (!orgidObj.refpk) {
            Notice({
                status: "warning",
                msg: langCheck("10181TM-000030", "pages", json)
            }); /* 国际化处理： 请选中业务单元*/
            return;
        }
        if (!templatePk) {
            Notice({
                status: "warning",
                msg: langCheck("10181TM-000031", "pages", json)
            }); /* 国际化处理： 请选择模板数据*/
            return;
        }
        switch (code) {
            case "copy":
                this.setState({
                    visible: true
                });
                break;
            case "edit":
                if (def1 === "menuitem") {
                    Ajax({
                        loading: true,
                        url: "/nccloud/platform/template/edittemplate.do",
                        data: {
                            appcode: appCode,
                            templateid: templatePk,
                            pk_org: orgidObj.refpk
                        },
                        success: function(res) {
                            let protocol = window.location.protocol || "http:";
                            if (location.port) {
                                // 动态取location协议
                                window.location.href =
                                    // window.open(
                                    "uclient://start/" +
                                    protocol +
                                    "//" +
                                    location.hostname +
                                    ":" +
                                    location.port +
                                    res.data.data;
                            } else {
                                // window.open('uclient://start/' + 'http://' + location.hostname + res.data.data);
                                window.location.href =
                                    "uclient://start/" +
                                    protocol +
                                    "//" +
                                    location.hostname +
                                    res.data.data;
                            }
                        },
                        error: function(res) {
                            alert("lm:" + res.message);
                        }
                    });
                } else {
                    openPage(`ZoneSetting`, false, {
                        templetid: templatePk,
                        status: "templateSetting-unit",
                        appcode: appCode, //绪鹏添加（实施态需要传参清除模板缓存）
                        pcode: pageCode
                    });
                }
                break;
            case "delete":
                let url;
                if (def1 === "menuitem") {
                    url = `/nccloud/platform/template/deletePrintTemplate.do`;
                } else {
                    url = `/nccloud/platform/template/deleteTemplateDetail.do`;
                }
                ControlTip({
                    status: "warning",
                    title: langCheck(
                        "10181TM-000038",
                        "pages",
                        json
                    ) /* 国际化处理： 删除*/,
                    msg: langCheck(
                        "10181TM-000039",
                        "pages",
                        json
                    ) /* 国际化处理： 你确认要删除吗？,你确认要删除吗*/,
                    fieldid: "delete", //自动化测试标记
                    onOk: () => {
                        Ajax({
                            url: url,
                            data: infoData,
                            info: {
                                name: langCheck(
                                    "10181TM-000009",
                                    "pages",
                                    json
                                ) /* 国际化处理： 模板设置*/,
                                action: langCheck(
                                    "10181TM-000038",
                                    "pages",
                                    json
                                ) /* 国际化处理： 删除*/
                            },
                            success: ({ data }) => {
                                if (data.success) {
                                    Notice({
                                        status: "success",
                                        msg: langCheck(
                                            "10181TM-000040",
                                            "pages",
                                            json
                                        ) /* 国际化处理： 删除成功*/
                                    });
                                    this.reqTreeTemData();
                                }
                            }
                        });
                    }
                });
                break;
            case "assign":
                this.setState({
                    alloVisible: true
                });
                break;
            case "browse":
                if (def1 === "menuitem") {
                    this.showModal();
                } else {
                    this.setState({
                        batchSettingModalVisibel: true
                    });
                }
                break;
            case "copyOthersApp":
                this.setState({
                    copyOthersModalVisible: true
                });
                //TOdo 发送ajax拿到模板名称和左侧树
                break;
            case "rename":
                this.setState({
                    reNameModal: true
                });
                break;
            case "export":
                let submitUrl = `/nccloud/platform/appregister/templetexport.do`;
                let params = {
                    pagecode: pageCode,
                    appcode: appCode
                };
                handleDownload(params, submitUrl, 1);
                break;
            default:
                break;
        }
    };
    componentWillMount = () => {
        let callback = json => {
            this.props.setJson(json);
        };
        getMulti({
            moduleId: "10181TM",
            domainName: "workbench",
            callback
        });
    };
    componentDidMount = () => {
        let {
            selectedKeys,
            setSelectedKeys,
            def1,
            expandedKeys,
            setExpandedKeys,
            appCode,
            pageCode,
            setAppCode,
            setPageCode,
            expandedTemKeys,
            setExpandedTemKeys,
            selectedTemKeys,
            setSelectedTemKeys,
            searchValue
        } = this.props;
        if (def1 !== "") {
            if (searchValue) {
                this.handleSearch(searchValue, this.handleExpanded);
            } else {
                this.reqTreeData();
            }
            setSelectedKeys(selectedKeys);
            setDef1(def1);
            setExpandedKeys(expandedKeys);
            setAppCode(appCode);
            setPageCode(pageCode);
            this.reqTreeTemData("historyData");
            setExpandedTemKeys(expandedTemKeys);
            setSelectedTemKeys(selectedTemKeys);
        } else {
            this.reqTreeData(this.setInformationFun);
        }
    };
    //保持选中数据信息一致
    setInformationFun = treeData => {
        let { setSelectedKeys, setDef1, setAppCode, setPageCode } = this.props;
        if (treeData && treeData.length > 0) {
            setSelectedKeys([treeData[0].pk]);
            setDef1(treeData[0].def1);
            setAppCode(treeData[0].appCode);
            if (treeData[0].pageCode) {
                setPageCode(treeData[0].pageCode);
            }
        }
    };
    //右侧树组装数据
    restoreTreeTemData = (templateType, eventType) => {
        let { treeTemBillData, treeTemPrintData } = this.state;
        let {
            def1,
            copyId,
            expandedTemKeys,
            setExpandedTemKeys,
            setSelectedTemKeys,
            setParentIdcon,
            setTemplatePk,
            setTemplateNameVal,
            setTemplateTitleVal,
            json
        } = this.props;
        let treeTemBillDataArray = this.props.treeTemBillData;
        let treeTemPrintDataArray = this.props.treeTemPrintData;
        let treeData = [];
        let treeInfo;
        if (templateType === "bill") {
            treeInfo = generateTemData(treeTemBillDataArray);
        } else if (templateType === "print") {
            treeInfo = generateTemData(treeTemPrintDataArray);
        }
        let { treeArray, treeObj } = treeInfo;
        treeArray.map((item, index) => {
            const groupObj = {
                name: langCheck(
                    "10181TM-000042",
                    "pages",
                    json
                ) /* 国际化处理： 集团模板*/,
                title: langCheck(
                    "10181TM-000042",
                    "pages",
                    json
                ) /* 国际化处理： 集团模板*/,
                pk: "qazwsxedc1" + item.pk,
                code: "1001",
                parentId: "groupRoot",
                children: []
            };
            const orgIdObj = {
                title: langCheck(
                    "10181TM-000043",
                    "pages",
                    json
                ) /* 国际化处理： 组织模板*/,
                name: langCheck(
                    "10181TM-000043",
                    "pages",
                    json
                ) /* 国际化处理： 组织模板*/,
                pk: "qazwsxedc2" + item.pk,
                code: "1002",
                parentId: "groupRoot",
                children: []
            };
            item.children.push(groupObj);
            item.children.push(orgIdObj);
            for (const key in treeObj) {
                if (treeObj.hasOwnProperty(key)) {
                    if (item.templateId === treeObj[key][0].parentId) {
                        if (treeObj[key][0].type === "group") {
                            item.children[0].children.push(treeObj[key][0]);
                        } else if (treeObj[key][0].type === "org") {
                            item.children[1].children.push(treeObj[key][0]);
                        }
                    }
                }
            }
        });
        //处理树数据
        treeData = treeInfo.treeArray;
        treeData = generateTreeData(treeData);
        if (templateType === "bill") {
            if (def1 === "apppage") {
                if (treeData.length > 0) {
                    if (!eventType) {
                        let newinitKeyArray = [];
                        newinitKeyArray.push(treeData[0].key);
                        setSelectedTemKeys(newinitKeyArray);
                        setParentIdcon(treeData[0].parentId);
                        setTemplatePk(treeData[0].pk);
                        setTemplateNameVal(treeData[0].name);
                    } else if (eventType && eventType === "copy") {
                        treeData.map(item => {
                            if (item.children && item.children.length > 0) {
                                item.children.map(ele => {
                                    if (
                                        ele.children &&
                                        ele.children.length > 0
                                    ) {
                                        ele.children.map(e => {
                                            if (copyId === e.key) {
                                                if (e.type === "group") {
                                                    let cc =
                                                        "qazwsxedc1" +
                                                        e.parentId;
                                                    let array = [
                                                        ...expandedTemKeys,
                                                        e.parentId,
                                                        cc
                                                    ];
                                                    setExpandedTemKeys(array);
                                                } else if (e.type === "org") {
                                                    let cc =
                                                        "qazwsxedc2" +
                                                        e.parentId;
                                                    let array = [
                                                        ...expandedTemKeys,
                                                        e.parentId,
                                                        cc
                                                    ];
                                                    setExpandedTemKeys(array);
                                                }
                                            }
                                        });
                                    }
                                });
                            }
                        });
                    }
                } else {
                    this.props.setParentIdcon("");
                }
                //模板名称显示[默认]
                treeData.forEach(item => {
                    if (item.children && item.children.length > 0) {
                        item.children.map(type => {
                            type.children &&
                                type.children.map(ele => {
                                    if (ele.isDefault === "y") {
                                        ele.name =
                                            ele.name +
                                            langCheck(
                                                "10181TM-000041",
                                                "pages",
                                                json
                                            ); /* 国际化处理： [默认]*/
                                    }
                                });
                        });
                    }
                });
            }
            treeTemBillData = treeData;
            this.setState({
                treeTemBillData
            });
        } else if (templateType === "print") {
            if (def1 === "menuitem") {
                if (treeData.length > 0) {
                    if (!eventType) {
                        let newinitKeyArray = [];
                        newinitKeyArray.push(treeData[0].key);
                        setSelectedTemKeys(newinitKeyArray);
                        setParentIdcon(treeData[0].parentId);
                        setTemplatePk(treeData[0].pk);
                        setTemplateNameVal(treeData[0].name);
                        setTemplateTitleVal(treeData[0].code);
                    } else if (eventType && eventType === "copy") {
                        treeData.map(item => {
                            if (item.children && item.children.length > 0) {
                                item.children.map(ele => {
                                    if (
                                        ele.children &&
                                        ele.children.length > 0
                                    ) {
                                        ele.children.map(e => {
                                            if (copyId === e.key) {
                                                if (e.type === "group") {
                                                    let cc =
                                                        "qazwsxedc1" +
                                                        e.parentId;
                                                    let array = [
                                                        ...expandedTemKeys,
                                                        e.parentId,
                                                        cc
                                                    ];
                                                    setExpandedTemKeys(array);
                                                } else if (e.type === "org") {
                                                    let cc =
                                                        "qazwsxedc2" +
                                                        e.parentId;
                                                    let array = [
                                                        ...expandedTemKeys,
                                                        e.parentId,
                                                        cc
                                                    ];
                                                    setExpandedTemKeys(array);
                                                }
                                            }
                                        });
                                    }
                                });
                            }
                        });
                    }
                } else {
                    this.props.setParentIdcon("");
                }
                //打印模板名称显示[默认]
                treeData.forEach(item => {
                    if (item.children && item.children.length > 0) {
                        item.children.map(type => {
                            type.children &&
                                type.children.map(ele => {
                                    if (ele.isDefault === "y") {
                                        ele.name =
                                            ele.name +
                                            langCheck(
                                                "10181TM-000041",
                                                "pages",
                                                json
                                            ); /* 国际化处理： [默认]*/
                                    }
                                });
                        });
                    }
                });
            }
            treeTemPrintData = treeData;
            this.setState({
                treeTemPrintData
            });
        }
    };
    onExpand = (typeSelect, expandedKeys) => {
        switch (typeSelect) {
            case "systemOnselect":
                this.props.setExpandedKeys(expandedKeys);
                this.setState({
                    autoExpandParent: false
                });
                break;
            case "templateOnselect":
                this.props.setExpandedTemKeys(expandedKeys);
                this.setState({
                    autoExpandTemParent: false
                });
                break;
            default:
                break;
        }
    };
    //加载右侧模板数据
    onSelectQuery = (key, e) => {
        const { orgidObj, selectedKeys, json } = this.props;
        if (!orgidObj.refpk) {
            Notice({
                status: "warning",
                msg: langCheck("10181TM-000030", "pages", json)
            }); /* 国际化处理： 请选中业务单元*/
            return;
        }
        if (key.length > 0) {
            this.props.setSelectedKeys(key);
            this.props.setDef1(e.selectedNodes[0].props.refData.def1);
            this.props.setAppCode(e.selectedNodes[0].props.refData.appCode);
            this.props.setPageCode(e.selectedNodes[0].props.refData.code);
            this.setState(
                {
                    autoExpandParent: true
                },
                this.reqTreeTemData
            );
        } else {
            if (selectedKeys) {
                this.reqTreeTemData();
            }
        }
    };
    //请求右侧树数据
    reqTreeTemData = eventType => {
        const { pageCode, appCode, def1, orgidObj } = this.props;
        let infoData = {
            pageCode: pageCode,
            appCode: appCode,
            orgId: orgidObj.refpk
        };
        if (!infoData.pageCode) {
            return;
        }
        if (def1 === "apppage") {
            infoData.templateType = "bill";
            this.reqTreeTemAjax(infoData, "bill", eventType);
        } else if (def1 === "menuitem") {
            if (infoData.pageCode) {
                delete infoData.pageCode;
            }
            infoData.templateType = "print";
            this.reqTreeTemAjax(infoData, "print", eventType);
        }
    };
    //请求右侧树数据ajax方法封装
    reqTreeTemAjax = (infoData, templateType, eventType) => {
        let { json, selectedTemKeys } = this.props;
        let _this = this;
        Ajax({
            url: `/nccloud/platform/template/getTemplatesOfPage.do`,
            data: infoData,
            info: {
                name: langCheck(
                    "10181TM-000009",
                    "pages",
                    json
                ) /* 国际化处理： 模板设置*/,
                action: langCheck(
                    "10181TM-000044",
                    "pages",
                    json
                ) /* 国际化处理： 参数查询*/
            },
            success: ({ data }) => {
                if (data.data.length > 0) {
                    //保存TS值
                    _this.setState({
                        saveLeftArrTS: data.data
                    });
                }
                if (data.success) {
                    if (templateType === "bill") {
                        this.props.setTreeTemBillData(data.data);
                        this.restoreTreeTemData(templateType, eventType);
                    } else if (templateType === "print") {
                        this.props.setTreeTemPrintData(data.data);
                        this.restoreTreeTemData(templateType, eventType);
                        // 用于解决首次复制模板无nodeKey
                        if (selectedTemKeys && data.data && data.data.length) {
                            data.data.map((item, index) => {
                                if (item.templateId === selectedTemKeys[0]) {
                                    this.props.setNodeKey(item.nodeKey);
                                }
                            });
                        }
                    }
                }
            }
        });
    };
    //单据模板树的onSelect事件
    onTemSelect = (key, e) => {
        const { def1 } = this.props;
        if (def1 === "apppage") {
        } else if (def1 === "menuitem") {
            if (key.length > 0) {
                this.props.setNodeKey(e.selectedNodes[0].props.refData.nodeKey);
            } else if (
                e.node.props &&
                e.node.props.refData &&
                e.node.props.refData.nodeKey
            ) {
                // 用于解决首次复制模板无nodeKey
                this.props.setNodeKey(e.node.props.refData.nodeKey);
            }
        }
        if (key.length > 0) {
            let templateName = e.selectedNodes[0].props.refData.name;
            let defaultIndex = templateName.indexOf(
                langCheck("10181TM-000041", "pages", this.props.json)
            ); /* 国际化处理： [默认]*/
            if (defaultIndex !== -1) {
                //模板名称截取掉[默认]
                templateName = templateName.slice(0, defaultIndex);
            }
            this.props.setSelectedTemKeys(key);
            this.props.setTemplatePk(
                e.selectedNodes[0].props.refData.templateId
            );
            this.props.setParentIdcon(
                e.selectedNodes[0].props.refData.parentId
            );
            this.props.setTemplateNameVal(templateName);
            this.props.setTemplateTitleVal(
                e.selectedNodes[0].props.refData.code
            );
            this.props.setTemplateType(e.selectedNodes[0].props.refData.type);
        }
    };
    /**
     * tree 数据请求
     */
    reqTreeData = callBack => {
        let { json } = this.props;
        Ajax({
            url: `/nccloud/platform/appregister/querymenuitemstree.do`,
            info: {
                name: langCheck(
                    "10181TM-000002",
                    "pages",
                    json
                ) /* 国际化处理： 应用注册模块*/,
                action: langCheck(
                    "10181TM-000045",
                    "pages",
                    json
                ) /* 国际化处理： 查询*/
            },
            success: ({ data }) => {
                if (data.success && data.data.length > 0) {
                    this.props.setTreeData(data.data);
                    if (callBack && typeof callBack === "function") {
                        callBack(data.data);
                    }
                }
            }
        });
    };
    //树点击事件的汇总
    onSelect = (typeSelect, key, e) => {
        switch (typeSelect) {
            case "systemOnselect":
                this.onSelectQuery(key, e);
                break;
            case "templateOnselect":
                this.onTemSelect(key, e);
                break;
            default:
                break;
        }
    };
    //tree的查询方法
    onChange = e => {
        const value = e.target.value;
        if (value) {
            this.props.setSearchValue(value);
        } else {
            this.reqTreeData(this.setInformationFun);
            const expandedKeys = ["00"];
            this.props.setExpandedKeys(expandedKeys);
            this.props.setSearchValue("");
        }
    };
    //树查询
    handleTreeSearch = value => {
        this.handleSearch(value, this.handleExpanded);
    };
    handleExpanded = dataList => {
        const expandedKeys = dataList.map((item, index) => {
            return item.pk;
        });
        expandedKeys.push("00");
        this.props.setExpandedKeys(expandedKeys);
        this.setState({
            autoExpandParent: true
        });
    };
    handleSearch = (value, callback) => {
        let { json } = this.props;
        let fn = () => {
            Ajax({
                url: `/nccloud/platform/appregister/searchappmenuitem.do`,
                data: {
                    search_content: value,
                    containAppPage: true
                },
                info: {
                    name: langCheck(
                        "10181TM-000046",
                        "pages",
                        json
                    ) /* 国际化处理： 菜单项*/,
                    action: langCheck(
                        "10181TM-000047",
                        "pages",
                        json
                    ) /* 国际化处理： 查询应用树*/
                },
                success: res => {
                    let { success, data } = res.data;
                    if (success && data) {
                        this.props.setTreeData(data);
                        callback(data);
                    }
                }
            });
        };
        DeferFn(fn); //搜索框ajax请求阻塞问题
    };
    //树的双击事件回调方法
    hanldeDoubleClick = (typeSelect, e, node) => {
        let nodeDataoMduleid;
        let expandedKeys = [];
        switch (typeSelect) {
            case "systemOnselect":
                nodeDataoMduleid = node.props.refData.pk;
                expandedKeys = this.props.expandedKeys.concat([
                    nodeDataoMduleid
                ]);
                this.props.setExpandedKeys(expandedKeys);
                break;
            case "templateOnselect":
                nodeDataoMduleid = node.props.refData.pk;
                let expandedTemKeys = this.props.expandedTemKeys.concat([
                    nodeDataoMduleid
                ]);
                this.props.setExpandedTemKeys(expandedTemKeys);
                break;
            default:
                break;
        }
    };
    /**
     * 查询框删除按钮
     */
    handleSearchDel = () => {
        this.reqTreeData(this.setInformationFun);
        const expandedKeys = ["00"];
        this.props.setExpandedKeys(expandedKeys);
        this.props.setSearchValue("");
    };
    //树组件的封装
    treeResAndUser = (
        data,
        typeSelect,
        hideSearch,
        selectedKeys,
        expandedKeys,
        autoExpandParent,
        typeClass
    ) => {
        const { searchValue, json } = this.props;
        const loop = data => {
            return data.map(item => {
                let { code, name, pk } = item;
                let text = `${code} ${name}`;
                if (code === "00") {
                    text = `${name}`;
                }
                const index = text.indexOf(searchValue);
                const beforeStr = text.substr(0, index);
                const afterStr = text.substr(index + searchValue.length);
                const title =
                    index > -1 ? (
                        <span>
                            {beforeStr}
                            <span style={{ color: "#f50" }}>{searchValue}</span>
                            {afterStr}
                        </span>
                    ) : (
                        <span>
                            <span> {text} </span>
                        </span>
                    );
                const fieldidClassName =
                    code === "00"
                        ? `fieldid_${name}_node`
                        : `fieldid_${code}_node`; //自动化测试标记
                if (item.children && item.children.length > 0) {
                    return (
                        <TreeNode
                            key={pk}
                            title={title}
                            refData={item}
                            icon={({ expanded }) => {
                                return (
                                    <Svg
                                        width={20}
                                        height={20}
                                        xlinkHref={
                                            expanded
                                                ? "#icon-wenjianjiadakai"
                                                : "#icon-wenjianjia"
                                        }
                                    />
                                );
                            }}
                            switcherIcon={({ expanded }) => {
                                return (
                                    <i
                                        className={`font-size-18 iconfont ${
                                            expanded
                                                ? "icon-shu_zk"
                                                : "icon-shushouqi"
                                        }`}
                                    />
                                );
                            }}
                            className={fieldidClassName}
                        >
                            {loop(item.children)}
                        </TreeNode>
                    );
                }
                return (
                    <TreeNode
                        icon={<span className="tree-dot" />}
                        key={pk}
                        title={title}
                        refData={item}
                        className={fieldidClassName}
                    />
                );
            });
        };
        return (
            <NCDiv
                className={typeClass ? typeClass : ""}
                fieldid="templateSettingUnit"
                areaCode={NCDiv.config.TREE}
            >
                {hideSearch ? (
                    ""
                ) : (
                    <div className="fixed-search-input">
                        <Search
                            className={searchValue && "sider-search-color"}
                            placeholder={langCheck(
                                "10181TM-000048",
                                "pages",
                                json
                            )} /* 国际化处理： 菜单查询*/
                            onChange={this.onChange}
                            onSearch={this.handleTreeSearch}
                            value={searchValue}
                            suffix={
                                searchValue.length > 0 ? (
                                    <i
                                        className="iconfont icon-qingkong"
                                        onClick={this.handleSearchDel}
                                    />
                                ) : (
                                    ""
                                )
                            }
                            fieldid="search"
                        />
                    </div>
                )}
                <NCDiv
                    fieldid="templateSettingUnit"
                    areaCode={NCDiv.config.TreeCom}
                >
                    {data.length > 0 && (
                        <Tree
                            showLine
                            showIcon
                            onExpand={(key, node) => {
                                this.onExpand(typeSelect, key);
                            }}
                            expandedKeys={expandedKeys}
                            onSelect={(key, node) => {
                                this.onSelect(typeSelect, key, node);
                            }}
                            autoExpandParent={autoExpandParent}
                            selectedKeys={selectedKeys}
                            onDoubleClick={(key, node) => {
                                this.hanldeDoubleClick(typeSelect, key, node);
                            }}
                        >
                            {loop(data)}
                        </Tree>
                    )}
                </NCDiv>
            </NCDiv>
        );
    };
    //预览摸态框显示方法
    setModalVisibel = visibel => {
        this.setState({ batchSettingModalVisibel: visibel });
    };
    //分配摸态框显示方法
    setAssignModalVisible = visibel => {
        this.setState({ alloVisible: visibel });
    };
    //参照的回调函数
    handdleRefChange = value => {
        let { refname, refcode, refpk } = value;
        let orgidObj = {};
        orgidObj["refname"] = refname;
        orgidObj["refcode"] = refcode;
        orgidObj["refpk"] = refpk;
        this.props.setOrgidObj(orgidObj);
        if (this.props.orgidObj) {
            this.reqTreeTemData();
        }
    };
    //打印模板预览打开
    showModal = () => {
        this.setState({ previewPrintVisible: true }, () => {
            this.printModalAjax(this.props.templatePk);
        });
    };
    //打印模板预览关闭
    hideModal = () => {
        this.setState({ previewPrintVisible: false });
    };
    //打印模板预览界面请求
    printModalAjax = templateId => {
        let { json } = this.props;
        let infoData = {};
        infoData.templateId = templateId;
        const url = `/nccloud/platform/template/previewPrintTemplate.do`;
        Ajax({
            url: url,
            data: infoData,
            info: {
                name: langCheck(
                    "10181TM-000009",
                    "pages",
                    json
                ) /* 国际化处理： 模板设置*/,
                action: langCheck(
                    "10181TM-000049",
                    "pages",
                    json
                ) /* 国际化处理： 打印模板预览*/
            },
            success: ({ data }) => {
                if (data.success) {
                    document.getElementsByClassName(
                        "printContent"
                    )[0].innerHTML = data.data;
                }
            }
        });
    };
    ncUploadChange = info => {
        if (info.file.status !== "uploading") {
            console.log(info.file, info.fileList);
        }
        if (info.file.status === "done") {
            console.log(`${info.file.name} file uploaded successfully`);
            this.reqTreeTemData();
        } else if (info.file.status === "error") {
            console.log(`${info.file.name} file upload failed.`);
        }
    };
    render() {
        const {
            treeTemBillData,
            treeTemPrintData,
            visible,
            alloVisible,
            batchSettingModalVisibel,
            autoExpandParent,
            autoExpandTemParent,
            previewPrintVisible,
            copyOthersModalVisible,
            reNameModal
        } = this.state;
        const {
            selectedKeys,
            expandedKeys,
            def1,
            selectedTemKeys,
            expandedTemKeys,
            templatePk,
            orgidObj,
            json,
            parentIdcon,
            templateType
        } = this.props;
        const leftTreeData = [
            {
                code: "00",
                name: langCheck(
                    "10181TM-000050",
                    "pages",
                    json
                ) /* 国际化处理： 菜单树*/,
                pk: "00",
                children: createTree(this.props.treeData, "code", "pid")
            }
        ];
        const Btns = [
            {
                name: "导出",
                type: "primary",
                code: "export",
                isshow:
                    def1 == "apppage" &&
                    (parentIdcon !== "groupRoot" && parentIdcon)
            },
            {
                name: langCheck(
                    "10181TM-000051",
                    "pages",
                    json
                ) /* 国际化处理： 修改*/,
                code: "edit",
                fieldid: "edit",
                isshow:
                    def1 &&
                    parentIdcon &&
                    (parentIdcon !== "root" &&
                        parentIdcon !== "groupRoot" &&
                        templateType !== "group")
            },
            {
                name: langCheck(
                    "10181TM-000038",
                    "pages",
                    json
                ) /* 国际化处理： 删除*/,
                code: "delete",
                fieldid: "delete",
                isshow:
                    def1 &&
                    parentIdcon &&
                    (parentIdcon !== "root" &&
                        parentIdcon !== "groupRoot" &&
                        templateType !== "group")
            },
            {
                name: langCheck(
                    "10181TM-000052",
                    "pages",
                    json
                ) /* 国际化处理： 复制*/,
                type: "primary",
                code: "copy",
                fieldid: "copy",
                isshow:
                    def1 == "menuitem" &&
                    (parentIdcon !== "groupRoot" && parentIdcon) //打印模板复制
            },
            {
                name: langCheck(
                    "10181TM-000052",
                    "pages",
                    json
                ) /* 国际化处理： 复制*/,
                type: "primary",
                code: "copy",
                fieldid: "copy",
                isshow:
                    def1 == "apppage" &&
                    (parentIdcon !== "groupRoot" && parentIdcon), //页面模板复制
                divide: "btn"
            },
            {
                name: langCheck(
                    "10181TM-000065",
                    "pages",
                    json
                ) /* 国际化处理： 复制到其他应用*/,
                type: "primary",
                code: "copyOthersApp",
                dropdownFieldid: "copy", //下拉框fieldid
                fieldid: "copyOthersApp",
                isshow:
                    def1 == "apppage" &&
                    (parentIdcon !== "groupRoot" && parentIdcon),
                divide: "select"
            },
            {
                name: langCheck(
                    "10181TM-000063",
                    "pages",
                    json
                ) /* 国际化处理： 重命名*/,
                code: "rename",
                fieldid: "rename",
                isshow:
                    def1 == "apppage" &&
                    (parentIdcon !== "groupRoot" && parentIdcon)
            },
            {
                name: langCheck(
                    "10181TM-000053",
                    "pages",
                    json
                ) /* 国际化处理： 分配*/,
                code: "assign",
                fieldid: "assign",
                isshow:
                    def1 &&
                    parentIdcon &&
                    (parentIdcon !== "root" &&
                        parentIdcon !== "groupRoot" &&
                        templateType !== "group")
            },
            {
                name: langCheck(
                    "10181TM-000054",
                    "pages",
                    json
                ) /* 国际化处理： 浏览*/,
                code: "browse",
                fieldid: "browse",
                isshow: def1 && (parentIdcon !== "groupRoot" && parentIdcon)
            }
        ];
        let { c } = GetQuery(window.location.hash);
        return (
            <PageLayout
                className="nc-workbench-templateSetting"
                header={
                    <PageLayoutHeader>
                        <div style={{ display: "flex", alignItems: "center" }}>
                            <div className="header_in_icon">
                                <Appicon appCode={c} />
                                <NCDiv
                                    fieldid={langCheck(
                                        "10181TM-000032",
                                        "pages",
                                        json
                                    )}
                                    areaCode={NCDiv.config.Title}
                                    style={{ marginRight: 20 }}
                                >
                                    {langCheck("10181TM-000032", "pages", json)}
                                    {/* 国际化处理： 模板设置-业务单元*/}
                                </NCDiv>
                            </div>
                            <div style={{ width: "200px" }}>
                                <BusinessUnitTreeRefUnit
                                    value={orgidObj}
                                    placeholder={langCheck(
                                        "10181TM-000016",
                                        "pages",
                                        json
                                    )} /* 国际化处理： 默认业务单元*/
                                    fieldid="businessUnit" //自动化测试标记
                                    onChange={value => {
                                        this.handdleRefChange(value);
                                    }}
                                />
                            </div>
                        </div>
                        <div style={{ display: "flex" }}>
                            {def1 == "apppage" &&
                                (parentIdcon !== "groupRoot" &&
                                    parentIdcon) && (
                                    <NCUpload
                                        showUploadList={false}
                                        accept="application/json"
                                        action="/nccloud/platform/appregister/templetimport.do"
                                        headers={{
                                            authorization: "authorization-text"
                                        }}
                                        onChange={this.ncUploadChange}
                                    >
                                        <NCButton
                                            className="margin-left-6"
                                            key="import"
                                            colors="primary"
                                        >
                                            导入
                                        </NCButton>
                                    </NCUpload>
                                )}
                            <ButtonCreate
                                dataSource={Btns}
                                onClick={this.handleClick}
                            />
                        </div>
                    </PageLayoutHeader>
                }
            >
                <PageLayoutLeft>
                    {this.treeResAndUser(
                        leftTreeData,
                        "systemOnselect",
                        null,
                        selectedKeys,
                        expandedKeys,
                        autoExpandParent,
                        "templateSetting-searchTree workbench-scroll"
                    )}
                </PageLayoutLeft>
                <PageLayoutRight>
                    {def1 == "apppage" ? (
                        treeTemBillData.length > 0 ? (
                            <div className="templateTree-wrap">
                                <p className="template-title">
                                    {langCheck("10181TM-000033", "pages", json)}
                                </p>
                                {/* 国际化处理： 页面模板*/}
                                {this.treeResAndUser(
                                    treeTemBillData,
                                    "templateOnselect",
                                    "hideSearch",
                                    selectedTemKeys,
                                    expandedTemKeys,
                                    autoExpandTemParent,
                                    "templateTree"
                                )}
                            </div>
                        ) : (
                            <div className="noPageData">
                                <p className="noDataTip">
                                    {langCheck("10181TM-000034", "pages", json)}
                                </p>
                                {/* 国际化处理： 该页面无页面模板*/}
                            </div>
                        )
                    ) : def1 == "menuitem" ? (
                        treeTemPrintData.length > 0 ? (
                            <div className="templateTree-wrap">
                                <p className="template-title">
                                    {langCheck("10181TM-000035", "pages", json)}
                                </p>
                                {/* 国际化处理： 打印模板*/}
                                {this.treeResAndUser(
                                    treeTemPrintData,
                                    "templateOnselect",
                                    "hideSearch",
                                    selectedTemKeys,
                                    expandedTemKeys,
                                    autoExpandTemParent,
                                    "templateTree"
                                )}
                            </div>
                        ) : (
                            <div className="noPrintData">
                                <p className="noDataTip">
                                    {langCheck("10181TM-000036", "pages", json)}
                                </p>
                                {/* 国际化处理： 该页面无打印模板*/}
                            </div>
                        )
                    ) : (
                        <div className="noData">
                            <p className="noDataTip">
                                {langCheck("10181TM-000037", "pages", json)}
                            </p>
                            {/* 国际化处理： 请选择应用、页面查看数据*/}
                        </div>
                    )}
                </PageLayoutRight>
                {batchSettingModalVisibel && (
                    <PreviewModal
                        previewVisibel={batchSettingModalVisibel}
                        setModalVisibel={this.setModalVisibel}
                        templetData={{
                            pagecode: this.props.pageCode,
                            appcode: this.props.appCode,
                            templetid: templatePk
                        }}
                    />
                )}
                {copyOthersModalVisible && (
                    <CopyOthersMoudle
                        copyOthersModalVisible={copyOthersModalVisible}
                        setCopyOthersVisible={this.setCopyOthersVisible}
                        reqTreeTemData={this.reqTreeTemData}
                    />
                )}
                {visible && (
                    <CopyMoudle
                        visible={visible}
                        reqTreeTemData={this.reqTreeTemData}
                        setCopyVisible={this.setCopyVisible}
                    />
                )}
                {reNameModal && (
                    <ReNameTemplet
                        reNameModalVisible={reNameModal}
                        setReNameModal={this.setReNameModal}
                        reqTreeTemData={this.reqTreeTemData}
                        saveLeftArrTS={this.state.saveLeftArrTS}
                    />
                )}
                {previewPrintVisible && (
                    <Modal
                        title={langCheck(
                            "10181TM-000049",
                            "pages",
                            json
                        )} /* 国际化处理： 打印模板预览*/
                        maskClosable={false}
                        visible={previewPrintVisible}
                        onCancel={this.hideModal}
                        footer={null}
                        centered={true}
                        width={"calc(100vw - 64px)"}
                        className="viewPageTem"
                    >
                        <div className="printContent" />
                    </Modal>
                )}
                {alloVisible && (
                    <AssignComponent
                        alloVisible={alloVisible}
                        setAssignModalVisible={this.setAssignModalVisible}
                    />
                )}
            </PageLayout>
        );
    }
}
TemplateSettingUnit.propTypes = {
    treeData: PropTypes.array.isRequired,
    setTreeData: PropTypes.func.isRequired,
    setExpandedKeys: PropTypes.func.isRequired,
    setSelectedKeys: PropTypes.func.isRequired,
    setDef1: PropTypes.func.isRequired,
    setSelectedTemKeys: PropTypes.func.isRequired,
    setExpandedTemKeys: PropTypes.func.isRequired,
    setTreeTemBillData: PropTypes.func.isRequired,
    setTreeTemPrintData: PropTypes.func.isRequired,
    setTemplatePk: PropTypes.func.isRequired,
    setSearchValue: PropTypes.func.isRequired,
    setTemplateNameVal: PropTypes.func.isRequired,
    setTemplateTitleVal: PropTypes.func.isRequired,
    setPageCode: PropTypes.func.isRequired,
    setAppCode: PropTypes.func.isRequired,
    setParentIdcon: PropTypes.func.isRequired,
    setOrgidObj: PropTypes.func.isRequired,
    setCopyId: PropTypes.func.isRequired,
    selectedKeys: PropTypes.array.isRequired,
    expandedKeys: PropTypes.array.isRequired,
    treeTemBillData: PropTypes.array.isRequired,
    treeTemPrintData: PropTypes.array.isRequired,
    def1: PropTypes.string.isRequired,
    selectedTemKeys: PropTypes.array.isRequired,
    expandedTemKeys: PropTypes.array.isRequired,
    templatePk: PropTypes.string.isRequired,
    searchValue: PropTypes.string.isRequired,
    pageCode: PropTypes.string.isRequired,
    appCode: PropTypes.string.isRequired,
    parentIdcon: PropTypes.string.isRequired,
    templateTitleVal: PropTypes.string.isRequired,
    templateNameVal: PropTypes.string.isRequired,
    orgidObj: PropTypes.object.isRequired,
    nodeKey: PropTypes.array.isRequired,
    copyId: PropTypes.string.isRequired
};
export default connect(
    state => ({
        treeData: state.TemplateSettingUnitData.treeData,
        treeTemBillData: state.TemplateSettingUnitData.treeTemBillData,
        treeTemPrintData: state.TemplateSettingUnitData.treeTemPrintData,
        selectedKeys: state.TemplateSettingUnitData.selectedKeys,
        expandedKeys: state.TemplateSettingUnitData.expandedKeys,
        def1: state.TemplateSettingUnitData.def1,
        selectedTemKeys: state.TemplateSettingUnitData.selectedTemKeys,
        expandedTemKeys: state.TemplateSettingUnitData.expandedTemKeys,
        templatePk: state.TemplateSettingUnitData.templatePk,
        searchValue: state.TemplateSettingUnitData.searchValue,
        pageCode: state.TemplateSettingUnitData.pageCode,
        appCode: state.TemplateSettingUnitData.appCode,
        parentIdcon: state.TemplateSettingUnitData.parentIdcon,
        templateNameVal: state.TemplateSettingUnitData.templateNameVal,
        templateTitleVal: state.TemplateSettingUnitData.templateTitleVal,
        orgidObj: state.TemplateSettingUnitData.orgidObj,
        nodeKey: state.TemplateSettingUnitData.nodeKey,
        copyId: state.TemplateSettingUnitData.copyId,
        json: state.TemplateSettingUnitData.json,
        templateType: state.TemplateSettingUnitData.templateType
    }),
    {
        setTreeData,
        setTreeTemBillData,
        setTreeTemPrintData,
        setExpandedKeys,
        setSelectedKeys,
        setDef1,
        setSelectedTemKeys,
        setExpandedTemKeys,
        setTemplatePk,
        setSearchValue,
        setPageCode,
        setAppCode,
        setParentIdcon,
        setTemplateNameVal,
        setTemplateTitleVal,
        setOrgidObj,
        setNodeKey,
        setCopyId,
        setJson,
        setTemplateType
    }
)(TemplateSettingUnit);
