/**
 * form下载
 * 参数：
 * {
 *     params: {             // 操作需要的参数
 *          billId: '',
 *          ...
 *     },
 *     url: '',             // 操作的url
 *     enctype: 1/2/3       // form提交的方式  1和默认、multipart/form-data 2、application/x-www-form-urlencoded 3、text/plain
 * }
 */
export function handleDownload(params, url, enctype, target) {
    // 从ajax哪里取得，目的获取busiaction
    let busiaction = `${null}-${window.actionName || null}`;
    let appcode = params.appcode;
    const sysParamJson = {
        sys_busiaction: busiaction,
        sys_appcode: appcode,
        sys_ts: new Date().getTime()
    };
    enctype = (type => {
        switch (type) {
            case 1:
                return "multipart/form-data";
            case 2:
                return "application/x-www-form-urlencoded";
            case 3:
                return "text/plain";
            default:
                return "multipart/form-data";
        }
    })(enctype);

    const [attrs, el_form] = [
        {
            target: target ? target : "_blank",
            method: "POST",
            enctype,
            type: "hidden",
            action: url || ""
        },
        document.createElement("form")
    ];
    Object.assign(el_form, attrs);
    const config = { ...params, ...sysParamJson };
    for (let key in config) {
        if (config[key] instanceof Array) {
            config[key].forEach((item, index) => {
                const input = document.createElement("input");
                input.type = "hidden";
                input.name = key;
                input.value = item;
                el_form.appendChild(input);
            });
        } else {
            const input = document.createElement("input");
            input.type = "hidden";
            input.name = key;
            input.value = config[key];
            el_form.appendChild(input);
        }
    }
    document.body.appendChild(el_form);
    el_form.submit();
    document.body.removeChild(el_form);
    return false;
}
