import * as zonesetting from './action-type';

// 设置 区域参数信息 数据
export const setZoneData = (data) => {
	return {
		type: zonesetting.ZONESETTING,
		data
	};
};
export const updateSelectCard = (selectCard) => {
	return {
		type: zonesetting.UPDATESELECTCARD,
		selectCard: selectCard
	};
};
//更新areaList init：是否为初始化，第一次进页面时传
export const updateAreaList = (areaList, init = false) => {
	return {
		type: zonesetting.UPDATEAREALIST,
		areaList: areaList,
		init
	};
};

//当前选中区域
export const updateSelectArea = selectedArea => {
	return {
		type: zonesetting.UPDATESELECTAREA,
		selectedArea: selectedArea
	}
}

export const updatePreviewData = (previewData) => {
	return {
		type: zonesetting.PREVIEWDATA,
		previewData: previewData
	};
};
// 清空数据
export const clearData = () => {
	return {
		type: zonesetting.CLEARDATA
	};
};

//打开公式编辑器
export const toggleFormula = (type, flag) => {
	return {
		type: zonesetting.TOGGLEFORMULA,
		formula: {
			[type]: flag
		}
	}
}

//设置公式编辑器信息
export const setFormulaInfo = (formulaInfo) => {
	return {
		type: zonesetting.SETFORMULAINFO,
		formulaInfo
	}
}

//设置批量设置表格dataSource
export const updateBatchSource = (batchSource) => {
	return {
		type: zonesetting.UPDATEBATCHSOURCE,
		batchSource
	}
}
//更新批量设置表格dataSource
export const setCurrentBatchCell = (cell) => {
	return {
		type: zonesetting.SETCURRENTBATCHCELL,
		currentBatchCell: cell
	}
}
// 传递 区域 form 的区域值 
export const setZoneDataFun = (getFromData) => {
	return {
		type: zonesetting.SETZONEFORMFUN,
		getFromData
	};
};

//更新是否保存标示
export const updateSaveFlag = (flag) => {
	return {
		type: zonesetting.UPDATESAVEFLAG,
		isSave: flag
	};
};

//更新是否校验成功标示
export const updateAreaValidate = (validate) => {
	return {
		type: zonesetting.SETAREAVALIDATE,
		validate
	};
};