import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Input, Tree, Button } from 'antd';
import { DeferFn, langCheck, deepClone } from 'Pub/js/utils';
import Ajax from 'Pub/js/ajax.js';
import Notice from 'Components/Notice';
import HotKeysModal from "Components/HotKeysModal";
import platform from "Pub/js/platform";
const { NCDiv, NCButton } = platform.base;
const { TreeNode } = Tree;
const Search = Input.Search;

class CopyOthersMoudle extends Component {
    constructor(props) {
        super(props);
        this.state = {
            nameErrorMsg: '',//错误信息提示
            templateName: this.props.templateNameVal,
            treeData: [],//左侧树
            searchTreeDataSave: [],//搜索左侧树
            selectedKeys: [],
            checkedKeys: [],
            expandedKeys: [],
            autoExpandParent: false,
            saveLeafArr: [],//叶节点数组
            generateRightTreeData: [],
            rightTreeData: [], //右侧树
            rightCheckedKeys: [],//右侧树选中数据
            requestLeftTreeData: [],
            searchValue: '',
        };
    }

    componentWillMount () {
        let { appCode, json, orgidObj } = this.props;
        let { requestLeftTreeData } = this. state;
        //获取左侧树ajax
        Ajax({
            data: {
                'appCode': appCode,
                orgId: orgidObj.refpk,
            },
            url: `/nccloud/platform/template/queryAppAndPagesAction.do`,
            loading: true,
            info: {
                name: langCheck('10181TM-000009', "pages", json), /* 国际化处理： 模板设置*/ 
                action: langCheck('10181TM-000065', "pages", json) /* 国际化处理： 复制到其他应用*/ 
            },
            success: ({ data }) => {
                if (data.success) {
                    let isAllRootNode = [];
                    let isAllLeafNode = [];
                    let isAllNode = deepClone(data.data);
                    data.data.map(item => {
                        if (item.isleaf) {
                            item.children = [];
                            isAllLeafNode.push(item);
                        }else {
                            item.children = [];
                            isAllRootNode.push(item);
                        }
                    })
                    console.log('isAllRootNode',isAllRootNode);
                    console.log('isAllLeafNode',isAllLeafNode);
                    //拼接成两层树结构
                    isAllRootNode.map((itemRoot) => {
                        isAllLeafNode.map((itemLeaf) => {
                            if (itemRoot.code == itemLeaf.pid) {
                                itemRoot.children.push(itemLeaf);
                            }
                        })
                    })
                    console.log('isAllRootNode====',isAllRootNode);
                    console.log('isAllLeafNode====',isAllLeafNode);
                    this.setState({
                        treeData: isAllRootNode,
                        searchTreeDataSave: isAllRootNode,
                        saveLeafArr: isAllLeafNode,//全部叶节点
                        requestLeftTreeData: isAllNode
                    })
                }
            }
        });
    }
    componentDidMount() {}

    trim = (str) => {
        if (str) {
            return str.replace(/^\s+|\s+$/g, '');
        }
    };

    checkName = () => {
        let { json, templateNameVal } = this.props;
        let { templateName } = this.state;
        if (!this.trim(templateName)) {
            document.getElementById('templateName').classList.add('has-error');
            this.setState({
                nameErrorMsg: langCheck('10181TM-000062',"pages", json) //模板名称不能为空
            });
            return;
        }
        this.setState({
            nameErrorMsg: ''
        });
        document.getElementById('templateName').classList.remove('has-error');
    };

    //左侧部分选中树中的数据
    isPartSelectedLeft = () => {
        let { checkedKeys, saveLeafArr, requestLeftTreeData, treeData } = this.state;
        let checkedArr = [];
        let checkedTree = [];
        let isRoot = [];
        let isLeaf = [];
        console.log('checkedKeys----', checkedKeys)
        console.log('saveLeafArr----', saveLeafArr)
        console.log('requestLeftTreeData----', requestLeftTreeData)
        let leftCheckTree = checkedKeys;
        let leftTreeDataC = deepClone(treeData);
        let requestLeftTreeDataC = deepClone(requestLeftTreeData);
        if (checkedKeys.length > 0) {
            //选中一个子节点需要加上父节点treeData
            checkedKeys.map((itemChecked) => {
                leftTreeDataC.map((itemTr) => {
                    itemTr.children.map((itemChild) => {
                        if (itemChecked == itemChild.pk) {
                            leftCheckTree.push(itemTr.pk);
                        }
                    });
                });
            });
            leftCheckTree = Array.from(new Set(leftCheckTree));
            //遍历选中值
            leftCheckTree.map((checkedItem) => {
                requestLeftTreeDataC.map((leafItem) => {
                    if (checkedItem == leafItem.pk) {
                        checkedArr.push(leafItem);
                    }
                });
            });

            console.log('checkedArr----', checkedArr);
            //拆分父子节点
            checkedArr.map((item) => {
                if (item.isleaf) {
                    item.children = [];
                    isLeaf.push(item);
                }else {
                    item.children = [];
                    isRoot.push(item);
                }
            });
            //拼接成两层树结构
            isRoot.map((itemRoot) => {
                isLeaf.map((itemLeaf) => {
                    if (itemRoot.code == itemLeaf.pid) {
                        itemRoot.children.push(itemLeaf);
                    }
                })
            });
            checkedTree = isRoot;
        }else {
            return;
        }
        console.log('checkedTree----', checkedTree);
        this.setState({
            rightTreeData: checkedTree,
            rightCheckedKeys: [],
        })
    }

    //右侧部分选中树中的数据
    isPartSelectedRight = () => {
        let { rightCheckedKeys, rightTreeData } = this.state;
        let partRightTreeArr = deepClone(rightTreeData);
        if (rightCheckedKeys.length > 0) {
            rightCheckedKeys.map((rightItem) => {
                partRightTreeArr.map((treeItem, idx) => {
                    if (rightItem == treeItem.pk) {
                        partRightTreeArr.splice(idx,1);//删除两个数组不相同元素
                    }
                    else {
                        treeItem.children.map((secondItem, index) => {
                            if (rightItem == secondItem.pk) {
                                treeItem.children.splice(index,1);
                            }
                        });
                    }
                });
            });
        }else {
            return;
        }
        console.log('partRightTreeArr---', partRightTreeArr);
        this.setState({
            rightTreeData: partRightTreeArr
        });
    }

    //左侧全部选中树种数据
    isAllSelectedLeft = () => {
        console.log('this.state.saveLeafArr', this.state.saveLeafArr);
        this.setState({
            rightTreeData: this.state.treeData,
            rightCheckedKeys: []
        });
    }
    //右侧全部清空树种数据
    isAllSelectedRight = () => {
        this.setState({
            rightTreeData: [],
            rightCheckedKeys: []
        });
    }
    onExpand = (expandedKeys) => {
        this.setState({
          expandedKeys,
          autoExpandParent: false
        });
    }

    //左侧树选中部分
    onCheck = (checkedKeys) => {
        this.setState({ checkedKeys });
    }

    //右侧树选中部分
    onRightCheck = (rightCheckedKeys) => {
        this.setState({ rightCheckedKeys });
    }

    onSelect = (selectedKeys, info) => {
        this.setState({
            selectedKeys
        })
    }

    //左侧树渲染
    renderTreeNodes = data => data.map((item) => {
        //搜索关键字增加颜色
        let { searchValue } = this.state;
        let { name, pk } = item;
        let title;
        if (searchValue != '') {
            const index = name.indexOf(searchValue);
            const beforeStr = name.substr(0, index);
            const afterStr = name.substr(index + searchValue.length);
            title =
                index > -1 ? (
                    <span>
                        {beforeStr}
                        <span style={{ color: '#f50' }}>{searchValue}</span>
                        {afterStr}
                    </span>
                ) : (
                    <span>
                        <span> {name} </span>
                    </span>
                );
        } else {
            title = name;
        }
        const fieldidClassName = `fieldid_${name}_node`; //自动化测试标记
        //解析拼接数据
        if (item.children) {
          return (
            <TreeNode title={title} key={item.pk} dataRef={item} className={fieldidClassName}>
              {this.renderTreeNodes(item.children)}
            </TreeNode>
          );
        }
        return <TreeNode {...item} className={fieldidClassName}/>;
    });

    //右侧树渲染
    rightRenderTreeNodes = data => data.map((item) => {
        const fieldidClassName = `fieldid_${item.name}_node`; //自动化测试标记
        //解析拼接数据
        if (item.children) {
          return (
            <TreeNode title={item.name} key={item.pk} dataRef={item} className={fieldidClassName}>
              {this.renderTreeNodes(item.children)}
            </TreeNode>
          );
        }
        return <TreeNode {...item} className={fieldidClassName}/>;
    });

    //从其他应用复制成功执行
    handleOk = () => {
        let {
            templatePk,
            pageCode,
            appCode,
            def1,
            templateTitleVal,
            templateNameVal,
            json,
            orgidObj,
        } = this.props;
        let { templateName, rightTreeData } = this.state;
        if (templateName == '') {
            return;
        }
        // document.getElementById('templateName').classList.remove('has-error');
        //拼接成两层树结构
        let newRightTreeData = deepClone(rightTreeData);
        //为后台重组数据
        newRightTreeData.map((nItem) => {
            nItem.children.map((childItem) => {
                childItem.pageCode = childItem.code;
                delete childItem.appId;
                delete childItem.appCode;
                delete childItem.pk;
                delete childItem.name;
                delete childItem.pid;
                delete childItem.isleaf;
                delete childItem.children;
                delete childItem.code;
            })
            nItem.pageCodes = nItem.children;
            delete nItem.appId;
            delete nItem.pk;
            delete nItem.code;
            delete nItem.name;
            delete nItem.pid;
            delete nItem.isleaf;
            delete nItem.children;
        })        

        let infoData = {
            templateId: templatePk,
            templateName: templateName,
            templateType: 'bill',
            appAndPageCodes: newRightTreeData,
            orgId: orgidObj.refpk,
        };
        let url = `/nccloud/platform/template/copyTemplateToOtherAppsAction.do`;
        var _this = this;
        DeferFn(() => {
            Ajax({
                url: url,
                data: infoData,
                loading: true,
                info: {
                    name: langCheck('10181TM-000009', "pages", json) /* 国际化处理： 模板设置*/,
                    action: langCheck('10181TM-000021', "pages", json) /* 国际化处理： 模板复制*/
                },
                success: ({ data }) => {
                    if (data.success) {
                        Notice({ status: 'success', msg: data.data });
                        _this.props.reqTreeTemData('copyOthersApp');
                        _this.handleCancel();
                    }
                }
            });
        });
    };

    handleCancel = () => {
        this.props.setCopyOthersVisible(false);
    };
    //模板名称
    onChangeTemplateName = (e) => {
        this.setState({
            templateName: e.target.value
        })
    }

    //搜索树
    searchOnChange = (e) => {
        let { value } = e.target;
        this.setState({
            searchValue: value
        })
        let { treeData, searchTreeDataSave, requestLeftTreeData } = this.state;
        if (value == '') {
            this.setState({
                treeData: searchTreeDataSave
            })
            return;
        }
        
        //搜索逻辑
        let searchAllNodes = deepClone(requestLeftTreeData);
        let filterLeftAllNodes = searchAllNodes.filter((item) => {
            if (!item.isleaf) {
                return item;
            }else {
                if (item.name.indexOf(value) > -1) {
                    return item;
                }
            }
        });
        let faNodes = [];
        let leafNodes = [];
        filterLeftAllNodes.map((item) => {
            item.children = [];
            if (item.isleaf) {
                leafNodes.push(item);
            } else {
                faNodes.push(item);
            }
        });
        //构建树
        faNodes.map((rItem) => {
            leafNodes.map((lItem) => {
                if (rItem.code == lItem.pid) {
                    rItem.children.push(lItem);
                }
            });
        });

        let newTree = faNodes.filter((item) => {
            if (item.children.length > 0) {
                return item;
            }
        });
        console.log('newTree=------------', newTree);

        let searchTreeData = deepClone(newTree);
        this.setState({
            treeData: searchTreeData
        })
    }

    render() {
        let { templateNameVal, json } = this.props;
        console.log('templateNameVal', templateNameVal);
        let { templateName, treeData, selectedKeys, checkedKeys, expandedKeys, autoExpandParent, rightTreeData, rightCheckedKeys, nameErrorMsg  } = this.state;
        return (
            <HotKeysModal
                mask={true}
                title={langCheck('10181TM-000065', "pages", json)} /* 国际化处理： 复制到其他应用*/
                visible={this.props.copyOthersModalVisible}
                onOk={this.handleOk}
                okText={langCheck('10181TM-000013', "pages", json)} /* 国际化处理： 确定*/
                cancelText={langCheck('10181TM-000014', "pages", json)} /* 国际化处理： 取消*/
                centered={true}
                onCancel={this.handleCancel}
                maskClosable={false}
                width={720}
                className='copy-others-moudle'
                fieldid="copyOther"
            >
                <div className='copy-others-modal-top'>
                    <div 
                        className='copy-others-modal-header'
                    >
                        {langCheck('10181TM-000027', "pages", json)}
                    </div>
                    {/* 国际化处理： 模板名称：*/}
                    <Input
                        className='copy-others-input'
                        style={{ width: '45%' }}
                        placeholder={langCheck('10181TM-000064', "pages", json)} /* 国际化处理： 模板名称*/
                        defaultValue={templateNameVal}
                        onChange={this.onChangeTemplateName}
                        id='templateName'
                        value={templateName}
                        onBlur={this.checkName}
                        fieldid='templateName'
                    />
                    <p className='areaName-errorMsg'>{nameErrorMsg}</p>
                </div>
                <div className='copy-others-modal-bottom'>
                    <NCDiv className='left' fieldid='left' areaCode={NCDiv.config.TREE}>
                        <div className='title'>{langCheck('10181TM-000066', "pages", json)}</div>
                        {/* 国际化处理： 待选项*/}
                        <Search className='search' 
                            onChange={this.searchOnChange}
                            fieldid='search'
                        />
                        <NCDiv className='leftTree' fieldid='left' areaCode={NCDiv.config.TreeCom}>
                            <Tree
                                checkable
                                checkStrictly={false}//父子节点会关联
                                loadData={this.onLoadData}
                                showLine={true}
                                multiple={true}
                                autoExpandParent={autoExpandParent}
                                onExpand={this.onExpand}
                                expandedKeys={this.state.expandedKeys}
                                onSelect={this.onSelect}
                                selectedKeys={this.state.selectedKeys}
                                checkedKeys={this.state.checkedKeys}
                                onCheck={this.onCheck}
                            >
                                {this.renderTreeNodes(treeData)}
                            </Tree>
                        </NCDiv>
                    </NCDiv>
                    <div className='middle'>
                        <div className='tree-transfer-buttons'>
                            <NCButton
                                className="transfer-right "
                                fieldid="toRight"
                                onClick={this.isPartSelectedLeft}
                            >
                                <i className="iconfont icon-jiantouyou" />
                            </NCButton>
                            <NCButton 
                                className="transfer-right-double margin-top-8"
                                fieldid="toRightAll"
                                onClick={this.isAllSelectedLeft}
                            >
                                <i className="transfer-icon-1 iconfont icon-jiantouyou" />
                                <i className="transfer-icon-2 iconfont icon-jiantouyou" />
                            </NCButton>
                            <NCButton
                                className="transfer-left-double margin-top-25"
                                fieldid="toLeft"
                                onClick={this.isAllSelectedRight}
                            >
                                <i className="transfer-icon-1 iconfont icon-jiantouzuo" />
                                <i className="transfer-icon-2 iconfont icon-jiantouzuo" />
                            </NCButton>
                            <NCButton
                                className="transfer-left margin-top-8"
                                fieldid="toRightAll"
                                onClick={this.isPartSelectedRight}
                            >
                                <i className="iconfont icon-jiantouzuo" />
                            </NCButton>
                        </div>
                    </div>
                    <div className='right'>
                        <div className='title'>{langCheck('10181TM-000067', "pages", json)}</div>
                        {/* 国际化处理： 已选项*/}
                        <NCDiv className='rightTree' fieldid='right' areaCode={NCDiv.config.TreeCom}>
                            <Tree
                                checkable
                                loadData={this.onLoadData}
                                showLine={true}
                                multiple={true}
                                autoExpandParent={autoExpandParent}
                                // onExpand={this.onExpand}
                                // expandedKeys={this.state.expandedKeys}
                                // onSelect={this.onSelect}
                                // selectedKeys={this.state.selectedKeys}
                                checkedKeys={this.state.rightCheckedKeys}
                                onCheck={this.onRightCheck}
                            >
                                {this.rightRenderTreeNodes(rightTreeData)}
                            </Tree>
                        </NCDiv>
                    </div>
                </div>
            </HotKeysModal>
        );
    }
}

CopyOthersMoudle.propTypes = {
    templatePk: PropTypes.string.isRequired,
    pageCode: PropTypes.string.isRequired,
    appCode: PropTypes.string.isRequired,
    def1: PropTypes.string.isRequired,
    templateTitleVal: PropTypes.string.isRequired,
    templateNameVal: PropTypes.string.isRequired,
    json: PropTypes.object.isRequired
}

export default connect(
    (state) => ({
        templatePk: state.TemplateSettingUnitData.templatePk,
        pageCode: state.TemplateSettingUnitData.pageCode,
        appCode: state.TemplateSettingUnitData.appCode,
        def1: state.TemplateSettingUnitData.def1,
        templateTitleVal: state.TemplateSettingUnitData.templateTitleVal,
        templateNameVal: state.TemplateSettingUnitData.templateNameVal,
        treeTemPrintData: state.TemplateSettingUnitData.treeTemPrintData,
        json: state.TemplateSettingUnitData.json,
        orgidObj: state.TemplateSettingUnitData.orgidObj,
    }),
    {
    }
)(CopyOthersMoudle);
