import React, { Component } from "react";
import { connect } from "react-redux";
import { updatePreviewData } from "Store/ZoneSetting/action";
import { createPage } from "nc-lightapp-front";
import Ajax from 'Pub/js/ajax';
import { langCheck } from "Pub/js/utils";
import { getMulti } from "Pub/js/getMulti";
import HotKeysModal from "Components/HotKeysModal";
import Loading from "Components/Loading";
import { sortBy } from 'lodash';
import "./index.less";

//加载模板
function initTemplate(json) {
    const { requestUrl, requestData, templetData } = this.props;
    const { pagecode, appcode, templetid } = templetData;
    let url = requestUrl || '/nccloud/platform/templet/querypage.do';
    let data = requestData || {
        pagecode,
        appcode,
        templetid
    };
    Ajax({
        url,
        data,
        info: {
            name: langCheck('ZoneSetting-000028', "pages", json),/* 国际化处理： 模板*/
            action: langCheck('ZoneSetting-000029', "pages", json)/* 国际化处理： 模板预览*/
        },
        success: ({ data: { data } }) => {
            if (data) {
                this.props.meta.setMeta(data);
                //增加position排序
                requestData && requestData.forEach(area => {
                    if(data[area.code]) data[area.code].position = area.position;
                });
                this.props.updatePreviewData(data);
            }
        }
    });
}

//预览模态框组件类1909版
class PreviewModal extends Component {
    constructor(props) {
        super(props);
        this.state = {
            all_keys: [],
            json: {}
        };
    }
    componentWillReceiveProps(nextProps) {
        if (nextProps.previewData) {
            let reviewData = nextProps.previewData;
            let all_keys = [];
            for (const [k, v] of Object.entries(reviewData)) {
                let key = v.moduletype;
                let areaId = v.code;
                let formrelation = reviewData.formrelation;
                if (key === "table" || key === "form" || key === "search") {
                    //不渲染表单分组
                    if (key === "form" && formrelation && v.relationcode in formrelation) {

                    }else{
                        all_keys.push({
                            id: areaId,
                            name: v.name,
                            myAreaType: key,
                            position: v.position
                        });
                    }
                }
                if(key === "form"){
                    this.props.form.setFormStatus(areaId, "edit");
                }
                //表格区默认新增一行
                if (key === "table") {
                    // this.props.editTable.setStatus(areaId, "edit");
                    if (this.refs[areaId]) {
                        this.props.editTable.addRow(areaId);
                        this.refs[areaId] = null;
                    }
                }
            }
            if (all_keys && all_keys[0] && all_keys[0].position) {
                all_keys = sortBy(all_keys, item => item.position); //按照position排序
            }
            //  更新state;
            this.setState({
                all_keys: all_keys
            });
        }
    }
    //隐藏预览模态框
    showModalHidden = () => {
        this.props.setModalVisibel(false);
    };
    //创建预览内容的DOM结构
    createDom = () => {
        let { editTable, form, search } = this.props;
        let { createForm } = form;
        let { createEditTable } = editTable;
        let { NCCreateSearch } = search;
        let { all_keys } = this.state;
        let result = [];
        // 所有预览
        if (all_keys.length) {
            all_keys.map((val, i) => {
                switch (val.myAreaType) {
                    case "form":
                        result.push(
                            <div className="viewTemplateArea" key={`form${i}`}>
                                <div className="descrip">
                                    <span key={`forms${i}`}> ▼ </span>
                                    {`${val.name}`}
                                </div>
                                {createForm(val.id)}
                            </div>
                        );
                        break;
                    case "table":
                        result.push(
                            <div
                                className="viewTemplateArea"
                                key={`table${i}`}
                                ref={`${val.id}`}
                            >
                                <div className="descrip">
                                    <span key={`tables${i}`}> ▼ </span>
                                    {`${val.name}`}
                                </div>
                                {createEditTable(val.id, {})}
                            </div>
                        );
                        break;
                    case "search":
                        result.push(
                            <div
                                className="viewTemplateArea"
                                key={`search${i}`}
                            >
                                <div className="descrip">
                                    <span key={`searchs${i}`}> ▼ </span>
                                    {`${val.name}`}
                                </div>
                                {NCCreateSearch(val.id)}
                            </div>
                        );
                        break;
                    default:
                        break;
                }
            });
        }
        return all_keys.length == 0 ? <Loading style={{ width: '100%', height: 50 }}/> : result;
    };

    componentDidMount() {
        //多语
        let callback = json => {
            this.setState({
                json: json
            });
            initTemplate.call(this, json);
        };
        getMulti({
            moduleId: "ZoneSetting",
            domainName: "workbench",
            callback
        });
    }
    render() {
        let { json } = this.state;
        return (
            <HotKeysModal
                maskClosable={false}
                customHeader={langCheck(
                    "ZoneSetting-000121",
                    "pages",
                    json
                )} /* 国际化处理： 预览区*/
                // mask={false}
                visible={this.props.previewVisibel}
                onOk={this.onOkDialog}
                onCancel={this.showModalHidden}
                width="98%"
                className="viewPageTem"
                centered={true}
                footer={null}
                fieldid="preview"
            >
                {this.createDom()}
            </HotKeysModal>
        );
    }
}

PreviewModal = createPage({})(PreviewModal);

export default connect(
    state => {
        return {
            areaList: state.zoneSettingData.areaList,
            previewData: state.zoneSettingData.previewData
        };
    },
    {
        updatePreviewData
    }
)(PreviewModal);
